#animation-card {
    background-color: #687E8F;
    color: #252423;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    padding-top: 85px;
    padding-bottom: 1.2rem;
  }
  
  .animation-wrapper {
    width: 80%;
    padding-bottom: 40%;
  }
  
  .stagger-visualizer {
    position: absolute;
    width: 1100px;
    height: 550px;
    transform-origin: left top;
  }
  
  .stagger-visualizer .dots-wrapper {
    transform: translateZ(0);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .stagger-visualizer .dot {
    position: relative;
    z-index: 1;
    width: 23px;
    height: 23px;
    margin: 16px;
    background-color: currentColor;
    border-radius: 50%;
  }
  
  @media (min-width: 740px) {
    .stagger-visualizer .dot {
      background-color: transparent;
      background-image: linear-gradient(180deg, #FABBA9 8%, #FDD3AE 100%);
    }
  }
  
  .stagger-visualizer .cursor {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 37px;
    height: 37px;
    margin: 9px;
    background-color: currentColor;
    border-radius: 50%;
  }